@import "../../../scss/variables.scss";
@import "../../../scss/mixins.scss";

.input {
  width: 360px;
  height: 58px;
  padding: 12px 40px 12px 20px;
  box-sizing: border-box;
  border-radius: 40px;
  border: 2px solid $dark-gray;
  background: $white;
  font-family: $font-family;
  z-index: 0;
  position: relative;

  // убирает дефолтный показ иконки пароля в Microsoft Edge
  &::-ms-reveal {
    display: none;
  }

  /* кастомный псевдоэлемент для placeholder */
  // &::before {
  //   content: attr(
  //     placeholder
  //   ); // Отображаем текст placeholder в качестве содержимого псевдоэлемента
  //   color: $gray;
  //   opacity: 0.4;
  //   font-size: 16px;
  //   font-style: normal;
  //   font-weight: 300;
  //   line-height: 22px;
  //   position: absolute;
  //   pointer-events: none; // Позволяет щелкать через placeholder
  // }

  /* При фокусе убираем placeholder */
  &:focus {
    border: 2px solid $green;
    outline: none;

    &::before {
      display: none;
    }
  }
  // стили для ошибки импута
  &-error {
    @include error-input;
    font-family: $font-family;
  }

  &_size-middle {
    width: 300px;
  }

  &_type_card {
    width: 170px;
    padding: 18px 20px 18px 20px;
  }

  &__label {
    font-family: $font-family;
    font-size: 10px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
    position: absolute;
    top: 15px;
    transform: translateY(-50%);
    left: 22px;
    color: $dark-gray;
    pointer-events: none;
    transition: 0.3s ease-in-out;
    z-index: 1;
    padding-bottom: 2px;
  }

  &__text {
    @include text-info;
    // display: block;
    // min-height: 34px;
    // padding: 0;
    // margin: 0;
    // border: 0;
    align-self: self-start;

    &_error {
      color: $alert-red;
      padding-top: 5px;
      padding-left: 20px;
    }

    &_info {
      color: $dark-gray;
      padding-top: 5px;
      padding-left: 20px;
    }

    &_confirm {
      color: $dark-gray;
      font-size: 14px;
      line-height: 22px;
      padding: 0 7px;
      text-align: center;
    }
  }

  &__promo {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-right: none;
    max-width: 305px;
    border-color: $black;
    max-height: 54px;
  }

  &__subscription {
    border-radius: 32px;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
    border-right: none;
    border: none;
    width: 100%;
    height: 58px;
  }

  &__items {
    position: relative;
  }

  &__search {
    font-family: $font-family;
    width: 265px;
    position: relative;
    padding-left: 46px;
    background-color: $main-white;
    border: 2px solid $black;

    &::placeholder {
      color: $gray;
    }
  }
}
